.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #09d3ac;
}

h1 {
	font-family: 'Monoton', cursive;
	font-size: 50px;
	color: #0a101dbb;
}
h4 {
	font-family: 'Orbitron', sans-serif;
}

span {
	font-family: 'Merienda One', cursive;
}
